/**
 * name -- 
 * process -- keyframes
 */
type IniteData = {
  name?: string;
  process?: Array<{ time: string; data: string }>;
};
//
export function initAnimate(data: IniteData) {
  const list = (data.process || []) as [];
  let processStr = list.reduce(
    (str, item: Record<'time' | 'data', string>) => str + item.time + '{' + item.data + '}',
    ''
  );
  return `
        @keyframes ${data.name}{
            ${processStr}
        }
    `;
}
