/**
 * 
 * EVENTON -- 
 * EVENTOFF -- 
 * EVENTCLICK -- 
 * EVENTMOVE -- 
 * EVENTOVER -- 
 * EVENTLEAVE -- 
 * EVENTDOWN -- 
 * EVENTUP -- 
 * EVENTCHANGE -- change
 * DEFAULTSCENENAME -- scene
 * UNIQKEY -- 
 * SCENEIDNAME -- idname
 * LOADED -- 
 */
export enum _CONSTANT_ {
  EVENTON = 'on',
  EVENTOFF = 'off',
  EVENTCLICK = 'click',
  EVENTMOVE = 'move',
  EVENTOVER = 'over',
  EVENTLEAVE = 'leave',
  EVENTDOWN = 'down',
  EVENTUP = 'up',
  EVENTCHANGE = 'change',
  DEFAULTSCENENAME = 'scene',
  UNIQKEY = 'uuid',
  SCENEIDNAME = 'scene',
  GEOMETRYIDNAME = 'geometry',
  GROUPIDNAME = 'group',
  POPUPIDNAME = 'popup',
  LOADED = 'loaded',
  ONPROGRESS = 'progress',
  WATCHBYTE = 'byte',
  WATCHCOUNT = 'count',
  CURVE = 'curve'
}

/**
 * 
 */
export type _Events =
  | _CONSTANT_.EVENTCLICK
  | _CONSTANT_.EVENTMOVE
  | _CONSTANT_.EVENTLEAVE
  | _CONSTANT_.EVENTDOWN
  | _CONSTANT_.EVENTUP
  | _CONSTANT_.EVENTOVER
  | _CONSTANT_.LOADED
  | _CONSTANT_.ONPROGRESS;

/**
 * 
 */
export enum _CONSTANT_CAMERA_ {
  CubeCamera = 'CubeCamera',
  OrthographicCamera = 'OrthographicCamera',
  PerspectiveCamera = 'PerspectiveCamera',
}
/**
 * 
 */
export enum _CONSTANT_LIGHT_ {
  PointLight = 'PointLight',
  RectAreaLight = 'RectAreaLight',
  SpotLight = 'SpotLight',
}
/**
 * 
 */
export enum _CONSTANT_GEOMETRY_ {
  BoxGeometry = 'box',
  CircleGeometry = 'circle',
  ConeGeometry = 'cone',
  CylinderGeometry = 'cylinder',
  PlaneGeometry = 'plane',
  SphereGeometry = 'sphere',
}
/**
 * 
 */
export enum _CONSTANT_MATERAIL_ {
  MeshBasicMaterial = 'basic',
  MeshLambertMaterial = 'lamber',
  MeshSpriteMaterial = 'sprite'
}

/**
 * bus events
 */

export enum _CONSTANT_BUS_ {
  UPDATE_SCENE = 'UPDATE_SCENE',
  ADD_POPUP = 'ADD_POPUP',
}

/**
 * bus component load
 */
export enum _CONSTANT_LOAD_ {
  SCENE = _CONSTANT_.EVENTOFF,
}
